/* fonts  */
@font-face {
  font-family: IBM;
  src: url("/public/fonts/IBM/IBMPlexSansArabic-Regular.ttf");
  weight: 500px;
}
@font-face {
  font-family: IBM;
  src: url("/public/fonts/IBM/IBMPlexSansArabic-Regular.ttf");
  weight: 400px;
}
@font-face {
  font-family: IBM;
  src: url("/public/fonts/IBM/IBMPlexSansArabic-Regular.ttf");
  weight: 600px;
}
/* ----------------------------------------------------------------------- */
.App {
  background-color: #006bff;
}
.card {
  height: 100vh;
}
.card,
.contaner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* align-items: center; */
  color: white;
}

.contaner {
  background-color: #0b2f9f;
  padding: 30px;
  border-radius: 20px;
  box-shadow: 0 12px 2px rgba(0, 0, 0, 0.08);
  margin-top: 20px;
}
.header {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-end;
  gap: 20px;
  direction: rtl;
  margin-bottom: -10px;
}
hr {
  width: 100%;
}
.body {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.bottom,
.top {
  display: grid;
  grid-template-columns: auto auto;
  justify-content: flex-end;
  align-items: flex-end;
  gap: 10px;
}
.zraar {
  display: flex;
  justify-content: flex-start;
  margin: 20px 5px;
}
@media (max-width: 530px) {
  .body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-self: center;
  }
  .left {
    display: flex;
    justify-content: center;
    margin-left: 10px;
  }
  .card {
    height: 120vh;
  }
}
